#about {
    text-align: center;
    padding: 100px 20px;
    width: 100%;
    color: white;
    margin: 0 auto;
}

.about-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

#about .container {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding-top: 40px;
}

#about h2 {
    font-size: 2.2rem;
    margin-bottom: 60px;
    position: relative;
    display: inline-block;
    padding: 0 30px;
}

#about h2::before,
#about h2::after {
    content: '';
    position: absolute;
    width: 15px;
    height: 15px;
    border: 2px solid rgba(124, 77, 255, 0.5);
}

#about h2::before {
    left: -5px;
    top: -5px;
    border-right: none;
    border-bottom: none;
}

#about h2::after {
    right: -5px;
    bottom: -5px;
    border-left: none;
    border-top: none;
}

.intro-section {
    text-align: left;
    background: rgba(124, 77, 255, 0.03);
    border-radius: 25px;
    padding: 30px;
    border: 1px solid rgba(124, 77, 255, 0.1);
    height: 100%;
    min-height: 450px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    position: relative;
    overflow: hidden;
    width: 100%;
    max-width: 100%;
    margin: 0 auto;
}

.intro-section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg,
        transparent,
        rgba(124, 77, 255, 0.5),
        transparent
    );
}

.intro-title {
    font-size: 2rem;
    color: rgba(124, 77, 255, 0.9);
    margin-bottom: 30px;
    display: inline-block;
    position: relative;
    padding: 10px 0;
    background: none;
    transform: none;
}

.intro-text {
    padding: 0;
    background: none;
    border: none;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1;
}

.intro-text p {
    font-size: 1.1rem;
    line-height: 1.8;
    color: rgba(255, 255, 255, 0.9);
    margin-bottom: 20px;
}

.timelines-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    width: 100%;
    margin-bottom: 80px;
}

.timeline-section {
    background: rgba(124, 77, 255, 0.03);
    border-radius: 20px;
    padding: 30px;
    border: 1px solid rgba(124, 77, 255, 0.1);
    transition: transform 0.3s ease;
}

.timeline-section:hover {
    transform: translateY(-5px);
}

.timeline-section h3 {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 30px;
    color: rgba(124, 77, 255, 0.9);
    font-size: 1.4rem;
    position: relative;
    padding-bottom: 15px;
}

.timeline-section h3::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 60px;
    height: 2px;
    background: rgba(124, 77, 255, 0.5);
}

.timeline {
    position: relative;
    padding-left: 25px;
}

.timeline::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 2px;
    background: linear-gradient(to bottom,
        rgba(124, 77, 255, 0.1),
        rgba(124, 77, 255, 0.5),
        rgba(124, 77, 255, 0.1)
    );
}

.timeline-item {
    padding: 25px;
    background: rgba(124, 77, 255, 0.05);
    border-radius: 15px;
    margin-bottom: 25px;
    border: 1px solid rgba(124, 77, 255, 0.1);
    transition: all 0.3s ease;
    position: relative;
    opacity: 0;
    transform: translateY(20px);
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.timeline-item::before {
    content: '';
    position: absolute;
    left: -25px;
    top: 50%;
    width: 12px;
    height: 12px;
    background: rgba(124, 77, 255, 0.9);
    border-radius: 50%;
    transform: translateY(-50%);
    box-shadow: 0 0 15px rgba(124, 77, 255, 0.5);
    transition: all 0.3s ease;
}

.timeline-item:hover {
    transform: translateX(10px);
    background: rgba(124, 77, 255, 0.08);
}

.timeline-item:hover::before {
    transform: translateY(-50%) scale(1.2);
}

.timeline-wrapper {
    position: absolute;
    width: 100%;
    opacity: 0;
    height: auto;
    transform: translateX(30px);
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    pointer-events: none;
    visibility: hidden;
}

.timeline-wrapper.active {
    opacity: 1;
    transform: translateX(0);
    pointer-events: all;
    visibility: visible;
}

.timeline-wrapper.active .timeline-item {
    opacity: 1;
    transform: translateY(0);
}

.timeline-wrapper.active .timeline-item:nth-child(1) {
    transition-delay: 0.1s;
}

.timeline-wrapper.active .timeline-item:nth-child(2) {
    transition-delay: 0.2s;
}

.timeline-date {
    color: rgba(124, 77, 255, 0.9);
    font-weight: bold;
    margin-bottom: 10px;
    font-size: 0.9rem;
}

.timeline-item h4 {
    color: white;
    margin-bottom: 10px;
    font-size: 1.1rem;
}

.timeline-item p {
    color: rgba(255, 255, 255, 0.8);
    font-size: 0.95rem;
    line-height: 1.6;
}

.skills-grid-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    width: 100%;
    margin-top: 60px;
}

.skills-section {
    padding: 30px;
    background: rgba(124, 77, 255, 0.03);
    border-radius: 20px;
    border: 1px solid rgba(124, 77, 255, 0.1);
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.skills-section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(
        circle at var(--mouse-x, 50%) var(--mouse-y, 50%),
        rgba(124, 77, 255, 0.1),
        transparent 70%
    );
    opacity: 0;
    transition: opacity 0.3s ease;
    pointer-events: none;
}

.skills-section:hover::before {
    opacity: 1;
}

.skills-section h3 {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 25px;
    color: rgba(124, 77, 255, 0.9);
    font-size: 1.3rem;
    position: relative;
    padding-bottom: 15px;
}

.skills-section h3::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 40px;
    height: 2px;
    background: rgba(124, 77, 255, 0.5);
    transition: width 0.3s ease;
}

.skills-section:hover h3::after {
    width: 100%;
}

.skills-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
    flex-grow: 1;
}

.skill-item {
    background: rgba(124, 77, 255, 0.08);
    padding: 12px 15px;
    border-radius: 12px;
    font-size: 0.9rem;
    color: white;
    transition: all 0.3s ease;
    border: 1px solid rgba(124, 77, 255, 0.1);
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    min-height: 60px;
}

.skill-item::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        90deg,
        transparent,
        rgba(255, 255, 255, 0.1),
        transparent
    );
    transition: left 0.5s ease;
}

.skill-item:hover {
    transform: translateY(-3px) scale(1.05);
    background: rgba(124, 77, 255, 0.12);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

.skill-item:hover::before {
    left: 100%;
}

/* Styles spéciaux pour chaque section de compétences */
.soft-skills .skill-item {
    background: linear-gradient(
        135deg,
        rgba(124, 77, 255, 0.08) 0%,
        rgba(124, 77, 255, 0.12) 100%
    );
    border-radius: 15px 5px 15px 5px;
}

.soft-skills .skill-item:hover {
    background: linear-gradient(
        135deg,
        rgba(124, 77, 255, 0.12) 0%,
        rgba(124, 77, 255, 0.16) 100%
    );
}

.hard-skills .skill-item {
    background: linear-gradient(
        135deg,
        rgba(77, 124, 255, 0.08) 0%,
        rgba(77, 124, 255, 0.12) 100%
    );
    border-radius: 5px 15px 5px 15px;
}

.hard-skills .skill-item:hover {
    background: linear-gradient(
        135deg,
        rgba(77, 124, 255, 0.12) 0%,
        rgba(77, 124, 255, 0.16) 100%
    );
}

.mad-skills .skill-item {
    background: linear-gradient(
        135deg,
        rgba(98, 77, 255, 0.08) 0%,
        rgba(98, 77, 255, 0.12) 100%
    );
    border-radius: 5px 15px 5px 15px;
}

.mad-skills .skill-item:hover {
    background: linear-gradient(
        135deg,
        rgba(98, 77, 255, 0.12) 0%,
        rgba(98, 77, 255, 0.16) 100%
    );
}

/* Animation des compétences au survol */
@keyframes pulse {
    0% { transform: scale(1); }
    50% { transform: scale(1.05); }
    100% { transform: scale(1); }
}

.skill-item:hover {
    animation: pulse 1s infinite;
}

/* Styles pour le système de switch */
.career-switch-container {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    background: rgba(124, 77, 255, 0.03);
    border-radius: 25px;
    border: 1px solid rgba(124, 77, 255, 0.1);
}

.switch-buttons {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
    margin-bottom: 30px;
    padding: 8px;
    border-radius: 15px;
    background: rgba(124, 77, 255, 0.05);
}

.switch-button {
    background: rgba(124, 77, 255, 0.05);
    border: 1px solid rgba(124, 77, 255, 0.1);
    border-radius: 12px;
    padding: 15px;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    position: relative;
    overflow: hidden;
}

.switch-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--category-color, rgba(124, 77, 255, 0.8));
    opacity: 0;
    transition: opacity 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    z-index: 0;
}

.switch-button .icon {
    font-size: 1.2rem;
    color: rgba(124, 77, 255, 0.9);
    position: relative;
    z-index: 1;
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.switch-button:hover {
    transform: translateY(-2px);
}

.switch-button:hover::before {
    opacity: 0.1;
}

.switch-button.active {
    background: rgba(124, 77, 255, 0.15);
    border-color: rgba(124, 77, 255, 0.4);
    box-shadow: 0 4px 20px rgba(124, 77, 255, 0.15);
}

.switch-button span {
    z-index: 1;
    position: relative;
    font-weight: 500;
}

.switch-button .arrow-icon {
    position: absolute;
    right: 15px;
    opacity: 0;
    transform: translateX(-10px);
    transition: all 0.3s ease;
}

.switch-button.active .arrow-icon {
    opacity: 1;
    transform: translateX(0);
}

.career-content {
    position: relative;
    min-height: fit-content;
    height: auto;
    overflow: visible;
}

.timeline-wrapper {
    position: relative;
    width: 100%;
    opacity: 0;
    height: auto;
    transform: translateX(30px);
    transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
    pointer-events: none;
    display: none;
}

.timeline-wrapper.active {
    opacity: 1;
    transform: translateX(0);
    pointer-events: all;
    display: block;
}

.timeline {
    position: relative;
    padding-left: 25px;
}

.career-section {
    background: rgba(124, 77, 255, 0.03);
    border-radius: 25px;
    padding: 30px;
    border: 1px solid rgba(124, 77, 255, 0.1);
    position: relative;
    height: 100%;
    min-height: 450px;
    display: flex;
    flex-direction: column;
    overflow: visible;
    width: 100%;
}

.career-section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg,
        transparent,
        rgba(124, 77, 255, 0.5),
        transparent
    );
}

/* Media Queries */
@media (max-width: 1140px) {
    .about-grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 30px;
    }
}

@media (max-width: 1024px) {
    .about-grid {
        grid-template-columns: 1fr;
        max-width: 800px;
    }

    .intro-section, .career-section {
        width: 100%;
        max-width: 100%;
        min-height: auto;
    }

    .career-section {
        margin-bottom: 40px;
    }

    .career-content {
        min-height: 450px;
    }

    .timelines-container {
        grid-template-columns: 1fr;
        gap: 30px;
    }

    .timeline-section {
        max-width: 100%;
    }

    .skills-grid-container {
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    .intro-text {
        padding: 30px;
    }

    .skills-section {
        padding: 25px;
    }

    .skills-grid {
        gap: 12px;
    }

    .about-grid {
        padding: 0 15px;
    }

    .intro-section, .career-section {
        max-width: 800px;
        margin: 0 auto;
    }

    .switch-buttons {
        display: flex;
        flex-direction: row;
        gap: 10px;
        margin-bottom: 25px;
        padding: 6px;
        background: rgba(124, 77, 255, 0.05);
        border-radius: 12px;
    }

    .switch-button {
        flex: 1;
        padding: 12px 15px;
    }
}

@media (max-width: 900px) {
    .skills-grid-container {
        grid-template-columns: repeat(2, 1fr);
        gap: 25px;
    }

    .skills-section h3 {
        font-size: 1.2rem;
        margin-bottom: 20px;
    }
}

@media (max-width: 768px) {
    #about {
        padding: 80px 15px;
    }

    .intro-title {
        font-size: 1.8rem;
    }

    .intro-text {
        padding: 25px;
    }

    .intro-text p {
        font-size: 1rem;
    }

    .timeline-section {
        padding: 20px;
    }

    .skills-grid-container {
        grid-template-columns: 1fr;
        max-width: 500px;
        margin: 40px auto 0;
    }

    .skills-section {
        padding: 25px 20px;
    }

    .skills-grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 12px;
    }

    .skill-item {
        font-size: 0.9rem;
        min-height: 50px;
    }

    .switch-buttons {
        grid-template-columns: repeat(2, 1fr);
        gap: 8px;
        max-width: 400px;
        margin: 0 auto 25px;
    }

    .switch-button {
        aspect-ratio: 1;
        flex-direction: column;
        padding: 12px 8px;
        min-height: 90px;
        max-width: 90px;
        width: 100%;
        margin: 0 auto;
    }

    .switch-button .icon {
        font-size: 1.3rem;
        margin-bottom: 6px;
    }

    .switch-button span {
        font-size: 0.85rem;
        text-align: center;
    }

    .career-content {
        min-height: 500px;
    }

    .intro-section, .career-section {
        padding: 25px;
    }

    .career-content {
        min-height: 500px;
    }

    .about-grid {
        gap: 30px;
        max-width: 600px;
    }
}

@media (max-width: 480px) {
    #about {
        padding: 60px 10px;
    }

    #about h2 {
        font-size: 1.5rem;
        margin-bottom: 40px;
    }

    .intro-title {
        font-size: 1.5rem;
        padding: 8px 20px;
    }

    .intro-text {
        padding: 20px;
    }

    .timeline-section h3,
    .skills-section h3 {
        font-size: 1.2rem;
    }

    .timeline-item {
        padding: 20px;
    }

    .skills-grid-container {
        max-width: 100%;
        gap: 20px;
    }

    .skills-section {
        padding: 20px 15px;
    }

    .skills-grid {
        grid-template-columns: 1fr;
        gap: 10px;
    }

    .skill-item {
        font-size: 0.85rem;
        padding: 12px 10px;
        min-height: 45px;
    }

    .career-switch-container {
        padding: 15px;
        border-radius: 20px;
    }

    .switch-buttons {
        padding: 0;
        gap: 8px;
    }

    .switch-button {
        width: 100%;
        padding: 12px;
        font-size: 0.9rem;
        justify-content: center;
        min-height: 80px;
        max-width: 80px;
        padding: 10px 6px;
    }

    .switch-button .icon {
        font-size: 1.2rem;
        margin-bottom: 4px;
    }

    .switch-button span {
        font-size: 0.8rem;
    }

    .career-content {
        min-height: 600px;
    }

    .switch-button {
        padding: 10px 15px;
        font-size: 0.9rem;
    }

    .switch-button .arrow-icon {
        right: 10px;
    }

    .career-content {
        min-height: 600px;
    }

    .about-grid {
        padding: 0 10px;
        gap: 25px;
        max-width: 100%;
    }

    .intro-section, .career-section {
        padding: 20px;
    }
}

@media (max-width: 360px) {
    .skills-section {
        padding: 15px 12px;
    }

    .skills-section h3 {
        font-size: 1.1rem;
        margin-bottom: 15px;
    }

    .skill-item {
        font-size: 0.8rem;
        padding: 10px 8px;
        min-height: 40px;
    }
}
