.homePart {
  text-align: left;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0;
  order: 1;
}

.homePart h3 {
  font-size: 1.8rem;
  position: relative;
  margin-bottom: 20px;
  color: rgba(255, 255, 255, 0.9);
  text-shadow: 0 0 10px rgba(124, 77, 255, 0.2);
  transform-origin: left;
  animation: fadeInSlide 1s ease-out;
}
.homePart h3::after {
  content: '';
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, rgba(124, 77, 255, 0.8), transparent);
  position: absolute;
  left: 0;
  bottom: -5px;
  animation: expandLine 1s ease-out forwards;
}

.home-container {
  display: grid;
  grid-template-columns: 60% 40%;
  justify-content: space-between;
  align-items: center;
  min-height: 70vh;
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  gap: 20px;
  padding: 0 20px;
}
#home {
  padding: 120px 0 80px;
  color: #fff;
  position: relative;
  min-height: 100vh;
}

#home::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* background: rgba(0, 0, 0, 0.5); */
}

.highlight {
  position: relative;
  color: rgba(124, 77, 255, 0.9) !important;
  font-weight: bold;
  font-size: 3rem !important;
  transition: all 0.3s ease;
  text-shadow: 0 0 15px rgba(124, 77, 255, 0.2);
  background: linear-gradient(120deg, rgba(124, 77, 255, 0.8), rgba(179, 136, 255, 0.8));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% auto;
  animation: shine 5s ease-in-out infinite;
}

/* Suppression du .highlight:after qui contenait la ligne de soulignement */

@keyframes shine {
  0% {
    background-position: -100% center;
  }
  50% {
    background-position: 100% center;
  }
  100% {
    background-position: -100% center;
  }
}

.profile-image {
  position: relative;
  overflow: visible;
  width: 420px;
  height: 454px;
  border-radius: 80px 20px 80px 20px;
  border: 2px solid rgba(124, 77, 255, 0.3);
  box-shadow: 0 0 20px rgba(124, 77, 255, 0.1);
  margin-left: auto;
  cursor: pointer;
}

.profile-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

.profile-image-container {
  order: 2;
  display: flex;
  justify-content: flex-end;
}

.profile-image-mobile {
  display: none;
}

.speech-bubble {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(20px);
  background: rgba(124, 77, 255, 0.95);
  padding: 20px 25px;
  opacity: 0;
  transition: all 0.3s ease-out;
  color: white;
  font-size: 1.1rem;
  z-index: 10;
  white-space: normal;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  min-width: 220px;
  max-width: 270px;
  border-radius: 20px 20px 5px 20px;
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transform-origin: center bottom;
  pointer-events: none;
}

/* Nouvelle décoration pour la bulle */
.speech-bubble::before {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 100%;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 12px solid rgba(124, 77, 255, 0.95);
  transform: translateX(-50%);
}

.message-container {
  position: relative;
  min-height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.message {
  opacity: 1;
  transform: translateY(0);
  transition: all 0.3s ease-out;
  text-align: center;
  line-height: 1.4;
  background: linear-gradient(
    45deg,
    rgba(255, 255, 255, 0.9),
    rgba(255, 255, 255, 1)
  );
  background-clip: text;
  -webkit-background-clip: text;
  text-shadow: 0 0 15px rgba(255, 255, 255, 0.3);
}

.dots {
  display: flex;
  gap: 3px;
  margin-top: 5px;
  justify-content: center;
}

.dots span {
  font-size: 20px;
  line-height: 0;
  animation: bounce 1s infinite;
}

.dots span:nth-child(2) {
  animation-delay: 0.2s;
}

.dots span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes bounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-5px); }
}

@keyframes messageChange {
  0%, 25% {
    opacity: 1;
    transform: translateY(0);
  }
  33%, 90% {
    opacity: 0;
    transform: translateY(-20px);
  }
  100% {
    opacity: 0;
    transform: translateY(20px);
  }
}

@media (min-width: 809px) {
  .profile-image:hover img {
    transform: scale(0.98) rotate(-2deg);
    filter: blur(2px);
  }
  
  .profile-image:hover .speech-bubble {
    opacity: 1;
    transform: translateX(-50%) translateY(0);
  }

  .profile-image-container {
    display: flex;
    justify-content: flex-end;
  }
  
  .profile-image-mobile {
    display: none;
  }
}

@keyframes messagePop {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.hover-message {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 1.2rem;
  text-align: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  background: rgba(124, 77, 255, 0.2);
  padding: 15px 20px;
  border-radius: 10px;
  backdrop-filter: blur(5px);
  white-space: nowrap;
}

#home h2 {
  font-size: 3rem;
  margin-bottom: 0px;
  font-family: Poppins, sans-serif;
}

#home p {
  font-size: 1.5rem;
  margin-bottom: 0px;
}

#home p:nth-of-type(1) {
  font-size: 1.5rem;
  position: relative;
  color: rgba(255, 255, 255, 0.9);
  margin-bottom: 15px;
  animation: fadeInUp 0.8s ease-out 0.3s both;
  text-shadow: 0 0 10px rgba(124, 77, 255, 0.1);
}

#home p#description {
  text-align: left;
  font-size: 1.1rem;
  margin: 15px 0 20px 0;
  padding: 15px 20px;
  border-left: 2px solid rgba(124, 77, 255, 0.3);
  width: 100%;
  max-width: 600px;
  animation: fadeInRight 0.8s ease-out 0.6s both;
  position: relative;
  background: linear-gradient(90deg, 
    rgba(124, 77, 255, 0.05), 
    transparent
  );
  border-radius: 0 8px 8px 0;
  backdrop-filter: blur(5px);
}

#home p#description::after {
  content: '🚀';
  position: relative;
  display: inline-block;
  margin-left: 5px;
  font-size: 1.2rem;
  animation: gentleFloat 4s ease-in-out infinite;
  opacity: 0.9;
}

#home .btn {
  background: transparent;
  border: 1.5px solid rgba(124, 77, 255, 0.4);
  box-shadow: 0 0 15px rgba(124, 77, 255, 0.1);
  backdrop-filter: blur(5px);
  color: #fff;
  /* padding: 8px 30px; */
  height: 50px;
  width: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  border-radius: 3px;
  transition: 0.5s ease;
  margin-top: 30px;
  position: relative;
  z-index: 1;
}

#home .btn:hover {
  background: rgba(124, 77, 255, 0.2);
  border-color: rgba(124, 77, 255, 0.6);
  box-shadow: 0 0 20px rgba(124, 77, 255, 0.2);
  transform: translateY(-2px);
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px; /* Espace entre les boutons */
}
h2 {
  font-size: 2.5rem;
  margin-bottom: 40px;
  font-family: Poppins, sans-serif;
  position: relative;
}


#home p:nth-of-type(2) {
  font-size: 1.1rem; /* Taille personnalisée */
  color: #ffffff; /* Couleur du texte */
  margin: 5px 80px 20px 0; /* Optionnel : ajoute un espace au-dessus */
}

.social-icons {
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
  width: 100%;
  gap: 20px; /* Espace entre les icônes */
  margin-top: 0px;
}

.social-icons i {
  font-size: 1.5rem !important; /* Taille des icônes */
  color: #ffffff; /* Couleur des icônes */
  transition: color 0.3s ease;
}

.social-icons i:hover {
  color: rgba(124, 77, 255, 0.8);
}

@keyframes expandLine {
  from {
    width: 0;
  }
  to {
    width: 120px;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(-50%) translateX(0);
  }
  50% {
    transform: translateY(-50%) translateX(-10px);
  }
}

@keyframes gentleFloat {
  0% {
    transform: translate(0, 0) rotate(0deg);
  }
  25% {
    transform: translate(0, -5px) rotate(3deg);
  }
  50% {
    transform: translate(0, 0) rotate(0deg);
  }
  75% {
    transform: translate(0, 5px) rotate(-3deg);
  }
  100% {
    transform: translate(0, 0) rotate(0deg);
  }
}

@media (max-width: 1124px) {
  .profile-image {
    width: 380px;
    height: 454px;
  }
}

@media (max-width: 1048px) {
  .homePart h2 {
    font-size: 2.4rem !important;
    margin: 10px 0 15px 0;
    line-height: 1.3;
  }
  
  .homePart span {
    font-size: 2.4rem !important;
  }

  .profile-image {
    width: 380px;
    height: 420px;
    margin-right: 30px;
  }

  .home-container {
    gap: 30px;
    padding: 0 20px;
  }

  #home p#description {
    margin: 15px 40px 20px 0;
    font-size: 1rem;
  }
}

@media (max-width: 960px) {
  .homePart h3 {
    font-size: 1.5rem !important;
  }

  .homePart h2 {
    font-size: 1.8rem !important;
  }
  
  .homePart span {
    font-size: 1.8rem !important;
  }

  .profile-image {
    width: 340px;
    height: 400px;
    margin-right: 20px;
  }

  .home-container {
    grid-template-columns: 55% 45%;
  }

  #home p:nth-of-type(1) {
    font-size: 1.3rem;
  }
}

@media (max-width: 950px) {
  .profile-image {
    width: 320px;
    height: 454px;
  }
}

@media (max-width: 870px) {
  .profile-image {
    width: 300px;
    height: 380px;
    margin-right: 15px;
  }

  .home-container {
    gap: 20px;
  }

  .homePart {
    margin: 0 20px 0 20px;
  }

  #home p#description {
    margin: 15px 20px 20px 0;
  }

  .buttons {
    gap: 15px;
  }

  #home .btn {
    width: 180px;
    font-size: 0.95rem;
  }
}

@media (max-width: 808px) {
  .home-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 15px;
    gap: 10px;
  }

  .homePart {
    text-align: center;
    align-items: center;
    order: 0;
  }

  .profile-image-container {
    display: none;
  }

  .profile-image {
    display: block;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin: 30px auto 0;
    border: 2px solid rgba(124, 77, 255, 0.3);
    box-shadow: 
      0 0 20px rgba(124, 77, 255, 0.1),
      inset 0 0 20px rgba(124, 77, 255, 0.1);
    transform: translateY(-20px);
    animation: floatImage 6s ease-in-out infinite;
  }

  .profile-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
  }

  .profile-image-mobile {
    display: block;
    width: 180px;
    height: 180px;
    border-radius: 50%;
    margin: 20px auto;
    border: 2px solid rgba(124, 77, 255, 0.3);
    box-shadow: 
      0 0 20px rgba(124, 77, 255, 0.1),
      inset 0 0 20px rgba(124, 77, 255, 0.1);
    overflow: visible;
  }

  .profile-image-mobile img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
  }

  .content-mobile-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .homePart {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0;
  }

  #home p:nth-of-type(1) {
    font-size: 1.1rem;
    margin-bottom: 20px;
  }

  #home p#description {
    text-align: center;
    margin: 0;
    padding: 20px;
    border-left: none;
    background: linear-gradient(90deg, 
      rgba(124, 77, 255, 0.05) 0%,
      rgba(124, 77, 255, 0.08) 50%,
      rgba(124, 77, 255, 0.05) 100%
    );
    border-radius: 12px;
    width: 100%;
    max-width: 600px;
  }

  .speech-bubble {
    top: auto;
    bottom: -80px;
    border-radius: 20px;
    min-width: 200px;
  }

  .speech-bubble::before {
    bottom: auto;
    top: -12px;
    border-bottom: 12px solid rgba(124, 77, 255, 0.95);
    border-top: none;
  }

  .homePart {
    text-align: center;
    display: grid;
    justify-items: center;
    margin: 0 40px;
    order: -1;
  }

  .buttons {
    margin: 0px 0;
    font-size: 0.8rem;
    height: 50px;
    width: 320px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  #home p#description {
    text-align: center;
    margin: 5px 0 20px;
    padding: 15px 20px;
    border-left: none;
    background: linear-gradient(90deg, 
      rgba(124, 77, 255, 0.05) 0%,
      rgba(124, 77, 255, 0.08) 50%,
      rgba(124, 77, 255, 0.05) 100%
    );
    border-radius: 8px;
    width: 100%;
    max-width: 600px;
  }

  #home p:nth-of-type(1) {
    font-size: 1.1rem;
    color: #ffffff;
    margin: 0px 0 20px;
    text-align: center;
    width: 100%;
  }

  #home p:nth-of-type(2) {
    font-size: 1.1rem; /* Taille personnalisée */
    color: #ffffff; /* Couleur du texte */
    margin: 0 0 20px 0; /* Optionnel : ajoute un espace au-dessus */
  }

  #home {
    padding-bottom: 0;
    min-height: 70vh;
  }
}


@keyframes floatImage {
  0%, 100% {
    transform: translateY(-20px);
  }
  50% {
    transform: translateY(-30px);
  }
}

@keyframes pulse {
  0% { 
    transform: translateX(-50%) scale(1) rotate(0deg); 
  }
  50% { 
    transform: translateX(-50%) scale(1.05) rotate(1deg); 
  }
  100% { 
    transform: translateX(-50%) scale(1) rotate(0deg); 
  }
}