#skills {
    text-align: center;
    width: 100%;
    position: relative;
    z-index: 1;
    margin-bottom: 0px; /* Ajout d'une marge en bas */
    padding: 100px 0 0;
}

#skills h2 {
    font-size: 2.2rem;
    margin-bottom: 60px;
    position: relative;
    display: inline-block;
    padding: 0 30px;
}

#skills h2::before,
#skills h2::after {
    content: '';
    position: absolute;
    width: 15px;
    height: 15px;
    border: 2px solid rgba(124, 77, 255, 0.5);
}

#skills h2::before {
    left: -5px;
    top: -5px;
    border-right: none;
    border-bottom: none;
}

#skills h2::after {
    right: -5px;
    bottom: -5px;
    border-left: none;
    border-top: none;
}

.skillsPart {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.skills-navigation {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-bottom: 50px;
    width: 100%;
    padding: 10px 0;
    flex-wrap: wrap;
}

.category-button {
    background: rgba(124, 77, 255, 0.05);
    border: 1px solid rgba(124, 77, 255, 0.1);
    border-radius: 12px;
    padding: 15px;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    gap: 10px;
    position: relative;
    overflow: hidden;
}

.category-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--category-color);
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 0;
}

.category-button:hover {
    transform: translateY(-2px);
}

.category-button:hover::before {
    opacity: 0.1;
}

.category-button.active {
    background: rgba(124, 77, 255, 0.15);
    border-color: var(--category-color);
    box-shadow: 0 4px 20px rgba(124, 77, 255, 0.15);
}

.category-button .category-icon {
    font-size: 1.2rem;
    color: var(--category-color);
    position: relative;
    z-index: 1;
}

.skills-showcase {
    position: relative;
    min-height: 400px;
}

.skills-category {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s ease;
}

.skills-category.active {
    opacity: 1;
    visibility: visible;
}

.skills-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 25px;
    padding: 20px;
}

.skill-card {
    background: rgba(124, 77, 255, 0.05);
    border-radius: 16px;
    padding: 25px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    transition: all 0.3s ease;
    border: 1px solid rgba(124, 77, 255, 0.1);
    position: relative;
    overflow: hidden;
}

.skill-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(
        circle at var(--mouse-x, 50%) var(--mouse-y, 50%),
        var(--category-color),
        transparent 70%
    );
    opacity: 0;
    transition: opacity 0.3s ease;
    pointer-events: none;
}

.skill-card:hover::before {
    opacity: 0.1;
}

.skill-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 25px rgba(124, 77, 255, 0.15);
}

.skill-card-header {
    display: flex;
    align-items: center;
    gap: 15px;
}

.skill-icon {
    font-size: 2rem;
    color: var(--category-color);
    transition: transform 0.3s ease;
}

.skill-card:hover .skill-icon {
    transform: scale(1.1) rotate(5deg);
}

.skill-card h4 {
    color: white;
    margin: 0;
    font-size: 1.2rem;
}

.skill-description {
    color: rgba(255, 255, 255, 0.8);
    font-size: 0.9rem;
    line-height: 1.6;
    margin: 0;
}

.skill-tools {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    margin-top: auto;
}

.tool-tag {
    background: rgba(124, 77, 255, 0.1);
    color: var(--category-color);
    padding: 5px 12px;
    border-radius: 20px;
    font-size: 0.8rem;
    transition: all 0.3s ease;
    border: 1px solid rgba(124, 77, 255, 0.2);
}

.skill-card:hover .tool-tag {
    background: rgba(124, 77, 255, 0.15);
    transform: translateY(-2px);
}

@media (max-width: 1200px) {
    .skills-grid {
        grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
        gap: 20px;
    }
}

@media (max-width: 768px) {
    #skills {
        margin-bottom: 30px;
    }

    .skills-navigation {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 10px;
        padding: 10px;
        margin: 0 0 30px 0;
    }

    .category-button {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        aspect-ratio: 1;
        width: 100%;
        min-width: 70px;
        height: auto;
        padding: 10px;
        gap: 8px;
        border-radius: 10px;
    }

    .category-button .category-icon {
        font-size: 1.5rem;
        margin: 0;
    }

    .category-button span:not(.category-icon) {
        font-size: 0.8rem;
        line-height: 1.2;
    }

    .skills-grid {
        grid-template-columns: 1fr;
        gap: 15px;
        padding: 15px;
    }

    .skill-card {
        padding: 20px;
    }
}

@media (max-width: 480px) {
    #skills {
        margin-bottom: 20px;
        padding: 60px 0;
    }

    #skills h2 {
        font-size: 1.5rem;
        margin-bottom: 40px;
    }

    .skillsPart {
        padding: 0 15px;
    }

    .skills-navigation {
        gap: 8px;
        padding: 8px;
    }

    .category-button {
        padding: 8px 6px;
        min-width: 65px;
    }

    .category-button .category-icon {
        font-size: 1.3rem;
    }

    .category-button span:not(.category-icon) {
        font-size: 0.75rem;
    }

    .skill-card {
        padding: 15px;
    }

    .skill-icon {
        font-size: 1.5rem;
    }

    .skill-card h4 {
        font-size: 1.1rem;
    }

    .skill-description {
        font-size: 0.85rem;
    }

    .tool-tag {
        font-size: 0.75rem;
        padding: 4px 10px;
    }
}

@media (max-width: 360px) {
    .skills-navigation {
        gap: 6px;
        padding: 6px;
    }

    .category-button {
        padding: 6px 4px;
        min-width: 60px;
    }

    .category-button .category-icon {
        font-size: 1.2rem;
    }

    .category-button span:not(.category-icon) {
        font-size: 0.7rem;
    }
}
