#projects {
    padding: 80px 0;
    max-width: 1000px;
    margin: 0 auto;
    text-align: center;
}

#projects h2 {
    font-size: 2.2rem;
    margin-bottom: 60px;
    position: relative;
    display: inline-block;
    padding: 0 30px;
}

#projects h2::before,
#projects h2::after {
    content: '';
    position: absolute;
    width: 15px;
    height: 15px;
    border: 2px solid rgba(124, 77, 255, 0.5);
}

#projects h2::before {
    left: -5px;
    top: -5px;
    border-right: none;
    border-bottom: none;
}

#projects h2::after {
    right: -5px;
    bottom: -5px;
    border-left: none;
    border-top: none;
}

.projects-container {
    position: relative;
    max-width: 800px;
    margin: 0 auto;
    padding: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-button {
    background: rgba(124, 77, 255, 0.1);
    border: none;
    width: 40px;
    height: 40px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    transition: all 0.3s ease;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 10;
    backdrop-filter: blur(5px);
}

.nav-button.prev {
    left: -20px;
}

.nav-button.next {
    right: -20px;
}

.nav-button:hover {
    background: rgba(124, 77, 255, 0.2);
    transform: translateY(-50%) scale(1.1);
}

.nav-button:active {
    transform: translateY(-50%) scale(0.95);
}

.project-card {
    background: rgba(124, 77, 255, 0.05);
    border-radius: 16px;
    overflow: hidden;
    border: 1px solid rgba(124, 77, 255, 0.1);
    transition: transform 0.3s ease-out, opacity 0.3s ease-out, box-shadow 0.3s ease-out;
    will-change: transform;
    touch-action: pan-x;
    position: relative;
    width: 100%;
    max-width: 700px;
    user-select: none;
    touch-action: pan-y pinch-zoom;
    display: flex;
    flex-direction: column;
    max-height: none;
    transform-style: preserve-3d;
    -webkit-transform-style: preserve-3d;
    touch-action: none;
}

.project-card.dragging {
    transition: none;
    cursor: grabbing;
}

.project-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 15px 30px rgba(0, 0, 0, 0.2);
    border-color: var(--project-color);
}

.project-card:active {
    cursor: grabbing;
}

.project-card.sliding-left {
    transform: translateX(-50px) scale(0.92);
    opacity: 0;
}

.project-card.sliding-right {
    transform: translateX(50px) scale(0.92);
    opacity: 0;
}

.project-card.sliding-in {
    animation: slideIn 0.8s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
}

@keyframes slideIn {
    0% {
        opacity: 0;
        transform: translateX(50px) scale(0.92);
    }
    100% {
        opacity: 1;
        transform: translateX(0) scale(1);
    }
}

.project-image-container {
    position: relative;
    width: 100%;
    height: 250px;
    overflow: hidden;
}

.project-image-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.5s ease;
}

.project-card:hover .project-image-container img {
    transform: scale(1.05);
    filter: brightness(0.7);
}

.project-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
        to bottom,
        transparent,
        rgba(31, 26, 45, 0.9)
    );
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: all 0.3s ease;
}

.project-card:hover .project-overlay {
    opacity: 1;
}

.project-icon {
    font-size: 3rem;
    color: white;
    transform: translateY(20px);
    transition: all 0.3s ease;
}

.project-card:hover .project-icon {
    transform: translateY(0) rotate(5deg);
}

.project-content {
    padding: 25px;
    text-align: left;
    display: flex;
    flex-direction: column;
    gap: 15px;
    position: relative;
    overflow: visible;
}

.project-content h3 {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 1.3rem;
    color: var(--project-color);
    margin-bottom: 15px;
}

.project-content h3 svg {
    font-size: 1.3rem;
}

.project-description {
    color: rgba(255, 255, 255, 0.9);
    line-height: 1.5;
    margin-bottom: 20px;
    font-size: 0.95rem;
}

.project-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
    margin-bottom: 20px;
}

.project-tag {
    background: rgba(124, 77, 255, 0.1);
    color: var(--project-color);
    padding: 5px 12px;
    border-radius: 15px;
    font-size: 0.85rem;
    transition: all 0.3s ease;
    border: 1px solid rgba(124, 77, 255, 0.2);
}

.project-card:hover .project-tag {
    background: rgba(124, 77, 255, 0.15);
    transform: translateY(-2px);
}

.project-features {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 15px;
    margin-top: 20px;
}

.feature-item {
    background: rgba(124, 77, 255, 0.08);
    padding: 15px;
    border-radius: 10px;
    display: flex;
    align-items: flex-start;
    gap: 12px;
    transition: all 0.3s ease;
    border: 1px solid rgba(124, 77, 255, 0.1);
}

.feature-item:hover {
    transform: translateY(-3px);
    background: rgba(124, 77, 255, 0.12);
    border-color: var(--project-color);
}

.feature-icon {
    color: var(--project-color);
    font-size: 1.2rem;
    transition: transform 0.3s ease;
}

.feature-item:hover .feature-icon {
    transform: rotate(10deg);
}

.feature-content h4 {
    color: white;
    font-size: 0.95rem;
    margin-bottom: 6px;
}

.feature-content p {
    color: rgba(255, 255, 255, 0.8);
    font-size: 0.85rem;
    line-height: 1.4;
}

.project-dots {
    position: absolute;
    bottom: -35px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 12px;
    padding: 10px;
    z-index: 5;
}

.dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.2);
    border: 2px solid rgba(124, 77, 255, 0.4);
    cursor: pointer;
    transition: all 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
    padding: 0;
    position: relative;
}

.dot::after {
    content: '';
    position: absolute;
    top: -4px;
    left: -4px;
    right: -4px;
    bottom: -4px;
    border-radius: 50%;
    background: radial-gradient(circle at center, 
        rgba(124, 77, 255, 0.15) 0%,
        transparent 70%
    );
    opacity: 0;
    transition: opacity 0.3s ease;
}

.dot:hover::after {
    opacity: 1;
}

.dot.active {
    background: var(--project-color);
    transform: scale(1.4);
    border-color: rgba(255, 255, 255, 0.6);
    box-shadow: 0 0 15px var(--project-color);
}

.project-card > * {
    animation: fadeIn 0.3s ease forwards;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateX(10px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@media (hover: hover) {
    .nav-button {
        opacity: 0.6;
        transition: opacity 0.3s ease, transform 0.3s ease;
    }

    .projects-container:hover .nav-button {
        opacity: 1;
    }
}

@media (hover: none) {
    .nav-button {
        opacity: 0.8;
        background: rgba(124, 77, 255, 0.2);
    }
}

@media (min-width: 1025px) {
    .projects-container {
        max-width: 900px;
    }

    .project-card {
        max-width: 800px;
    }

    .project-image-container {
        height: 300px;
    }

    .project-features {
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }
}

@media (min-width: 768px) and (max-width: 1024px) {
    .projects-container {
        max-width: 700px;
        padding: 15px 30px;
    }

    .project-card {
        max-width: 600px;
    }

    .project-image-container {
        height: 250px;
    }

    .project-features {
        grid-template-columns: repeat(2, 1fr);
        gap: 15px;
    }

    .nav-button {
        width: 45px;
        height: 45px;
    }
}

@media (max-width: 1024px) {
    .projects-container {
        padding: 15px 40px;
    }

    .project-image-container {
        height: 250px;
    }
}

@media (max-width: 768px) {
    #projects {
        padding: 60px 0;
    }

    .projects-container {
        padding: 10px 30px;
    }

    .nav-button {
        width: 35px;
        height: 35px;
        font-size: 0.9rem;
    }

    .nav-button.prev {
        left: -5px;
    }

    .nav-button.next {
        right: -5px;
    }

    .project-card {
        position: relative;
        height: auto;
        max-height: 90vh;
        display: flex;
        flex-direction: column;
        touch-action: pan-y;
        -webkit-overflow-scrolling: touch;
        overscroll-behavior: contain;
    }

    .project-image-container {
        height: 160px;
        flex-shrink: 0;
    }

    .project-content {
        flex: 1;
        display: flex;
        flex-direction: column;
        gap: 15px;
        padding: 20px 15px 30px;
    }

    .project-description {
        font-size: 0.9rem;
        line-height: 1.6;
        margin-bottom: 10px;
    }

    .project-features {
        display: grid;
        grid-template-columns: 1fr;
        gap: 12px;
        padding-bottom: 10px;
    }

    .feature-item {
        background: rgba(124, 77, 255, 0.05);
        padding: 12px;
        border-radius: 10px;
        border: 1px solid rgba(124, 77, 255, 0.1);
        transition: transform 0.2s ease;
    }

    .feature-item {
        opacity: 0;
        transform: translateY(20px);
        animation: fadeInUp 0.5s forwards;
        animation-delay: calc(var(--index) * 0.1s);
    }
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@media (max-width: 767px) {
    .projects-container {
        padding: 10px 20px;
    }

    .project-card {
        max-width: 100%;
    }

    .project-image-container {
        height: 200px;
    }

    .project-features {
        grid-template-columns: 1fr;
        gap: 12px;
    }

    .nav-button {
        width: 40px;
        height: 40px;
        background: rgba(124, 77, 255, 0.2);
        backdrop-filter: blur(8px);
    }
}

@media (max-width: 480px) {
    #projects h2 {
        font-size: 1.5rem;
        margin-bottom: 30px;
    }

    .projects-container {
        padding: 10px 15px;
    }

    .nav-button {
        display: none;
    }

    .project-dots {
        bottom: -20px;
        gap: 8px;
    }

    .dot {
        width: 5px;
        height: 5px;
        border-width: 1px;
    }
    
    .dot.active {
        transform: scale(1.2);
    }

    .project-card {
        border-radius: 12px;
        max-height: 75vh;
        margin: 0 auto;
    }

    .project-image-container {
        height: 120px;
        border-radius: 12px 12px 0 0;
    }

    .project-content {
        padding: 12px;
        gap: 8px;
    }

    .project-content h3 {
        font-size: 1rem;
        margin-bottom: 4px;
        gap: 6px;
    }

    .project-content h3 svg {
        font-size: 1rem;
    }

    .project-tags {
        gap: 4px;
        margin: 4px 0 8px;
    }

    .project-tag {
        font-size: 0.7rem;
        padding: 3px 8px;
        border-radius: 10px;
    }

    .project-description {
        font-size: 0.85rem;
        line-height: 1.4;
        margin-bottom: 8px;
    }

    .project-features {
        gap: 8px;
        padding-bottom: 8px;
    }

    .feature-item {
        padding: 8px;
        gap: 8px;
        border-radius: 8px;
    }

    .feature-content h4 {
        font-size: 0.8rem;
        margin-bottom: 2px;
    }

    .feature-content p {
        font-size: 0.75rem;
        line-height: 1.3;
    }

    .feature-icon {
        font-size: 0.9rem;
    }
}

@media (max-width: 360px) {
    .project-card {
        max-height: 72vh;
    }

    .project-image-container {
        height: 100px;
    }

    .project-content {
        padding: 10px;
        gap: 6px;
    }

    .project-description {
        font-size: 0.8rem;
        line-height: 1.3;
    }

    .feature-item {
        padding: 6px;
    }

    .feature-content h4 {
        font-size: 0.75rem;
    }

    .feature-content p {
        font-size: 0.7rem;
        line-height: 1.2;
    }
}

@media (max-height: 700px) and (max-width: 480px) {
    .project-card {
        max-height: 82vh;
    }

    .project-image-container {
        height: 120px;
    }

    .project-content {
        padding: 12px 12px 20px;
        gap: 10px;
    }
}

@media (max-height: 600px) and (max-width: 480px) {
    .project-card {
        max-height: 85vh;
    }

    .project-image-container {
        height: 90px;
    }
}

.scroll-indicator {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    width: 40px;
    height: 40px;
    display: none;
    justify-content: center;
    align-items: center;
    background: rgba(124, 77, 255, 0.1);
    border-radius: 50%;
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.3s ease;
}

@media (max-width: 768px) {
    .scroll-indicator {
        display: flex;
        animation: fadeInOut 2s infinite;
    }
}

@keyframes fadeInOut {
    0%, 100% { opacity: 0; }
    50% { opacity: 1; }
}

@media (hover: none) and (pointer: coarse) {
    .nav-button {
        opacity: 1;
        background: rgba(124, 77, 255, 0.25);
        backdrop-filter: blur(8px);
    }
    
    .project-card {
        touch-action: pan-x;
        -webkit-user-select: none;
        user-select: none;
    }

    .project-overlay {
        opacity: 0.6;
    }
}
