#contact {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 1;
    padding-top: 0px; /* Ajout d'un padding en haut */
    padding: 100px 0;
}

#contact .container {
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    padding: 0 20px;
}

#contact h2 {
    font-size: 2.2rem;
    margin-bottom: 60px;
    position: relative;
    display: inline-block;
    padding: 0 30px;
}

#contact h2::before,
#contact h2::after {
    content: '';
    position: absolute;
    width: 15px;
    height: 15px;
    border: 2px solid rgba(124, 77, 255, 0.5);
}

#contact h2::before {
    left: -5px;
    top: -5px;
    border-right: none;
    border-bottom: none;
}

#contact h2::after {
    right: -5px;
    bottom: -5px;
    border-left: none;
    border-top: none;
}

/* Form Container */
#contact form {
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    padding: 40px;
    background: rgba(124, 77, 255, 0.03);
    border-radius: 20px;
    border: 1px solid rgba(124, 77, 255, 0.1);
    backdrop-filter: blur(10px);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.form-row {
    width: 100%;
    max-width: 600px;
}

#contact .name-row {
    display: flex;
    gap: 20px;
}

#contact .name-row .form-group {
    flex: 1;
}

/* Form Elements */
#contact .form-group {
    position: relative;
    width: 100%;
    margin-bottom: 20px;
}

#contact .form-group input,
#contact .form-group textarea {
    width: 100%;
    background: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(124, 77, 255, 0.2);
    border-radius: 8px;
    padding: 15px;
    color: #fff;
    font-size: 1rem;
    transition: all 0.3s ease;
}

#contact .form-group input {
    height: 50px;
}

#contact .form-group textarea {
    height: 150px;
    resize: none;
    font-family: inherit;
    line-height: 1.5;
}

#contact .form-group label {
    position: absolute;
    left: 15px;
    top: 25px;
    transform: translateY(-50%);
    color: rgba(255, 255, 255, 0.5);
    font-size: 1rem;
    transition: all 0.3s ease;
    pointer-events: none;
    padding: 0 5px;
    background: transparent;
}

#contact .form-group textarea ~ label {
    top: 30px;
}

/* Input States */
#contact .form-group input:focus,
#contact .form-group textarea:focus,
#contact .form-group input:not(:placeholder-shown),
#contact .form-group textarea:not(:placeholder-shown) {
    border-color: rgba(124, 77, 255, 0.4);
    outline: none;
    box-shadow: 0 0 0 2px rgba(124, 77, 255, 0.2);
}

#contact .form-group input:focus ~ label,
#contact .form-group textarea:focus ~ label,
#contact .form-group input:not(:placeholder-shown) ~ label,
#contact .form-group textarea:not(:placeholder-shown) ~ label {
    top: -12px;
    font-size: 0.85rem;
    color: rgba(124, 77, 255, 0.8);
    background: #1F1A2D;
}

#contact .form-group input::placeholder,
#contact .form-group textarea::placeholder {
    color: transparent;
}

/* Submit Button */
#contact button {
    width: 200px;
    height: 50px;
    margin: 20px 0;
    background: rgba(124, 77, 255, 0.1);
    border: 1.5px solid rgba(124, 77, 255, 0.4);
    border-radius: 8px;
    color: #fff;
    font-size: 1rem;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
}

#contact button:hover {
    background: rgba(124, 77, 255, 0.2);
    border-color: rgba(124, 77, 255, 0.6);
    box-shadow: 0 0 20px rgba(124, 77, 255, 0.2);
    transform: translateY(-2px);
}

/* Success Message */
.form-status {
    color: #7C4DFF;
    margin-top: 20px;
    animation: fadeIn 0.5s ease;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Responsive Breakpoints */
@media (max-width: 1200px) {
    #contact form {
        max-width: 700px;
    }
}

@media (max-width: 1024px) {
    #contact h2 {
        font-size: 2rem;
    }
    
    #contact form {
        padding: 30px;
    }

    .form-row {
        max-width: 100%;
    }
}

@media (max-width: 900px) {
    #contact {
        padding: 80px 0;
    }
    
    #contact .container {
        padding: 0 15px;
    }
}

@media (max-width: 770px) {
    #contact .name-row {
        flex-direction: column;
        gap: 30px;
    }

    #contact form {
        padding: 20px;
        gap: 30px;
    }

    #contact .form-group {
        margin-bottom: 0;
    }

    #contact button {
        width: 100%;
        max-width: 300px;
        margin: 0;
    }
}

@media (max-width: 768px) {
    #contact {
        padding-top: 30px;
    }
}

@media (max-width: 480px) {
    #contact {
        padding-top: 20px;
        padding: 60px 0;
    }

    #contact h2 {
        font-size: 1.5rem;
        margin-bottom: 40px;
    }

    #contact form {
        padding: 15px;
    }

    #contact .form-group label {
        font-size: 0.9rem;
    }

    #contact .form-group input,
    #contact .form-group textarea {
        font-size: 0.9rem;
        padding: 12px;
    }

    #contact button {
        height: 45px;
        font-size: 0.9rem;
    }
}
