/* Styles généraux pour le header */
header {
  background: rgba(18, 7, 44, 0.15);
  color: #fff;
  padding: 15px 0;
  text-align: center;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
  backdrop-filter: blur(15px);
  transition: all 0.4s ease;
  border-bottom: 1px solid rgba(124, 77, 255, 0.1);
}

header.scrolled {
  padding: 10px 0;
  background: rgba(18, 7, 44, 0.25);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 20px;
}

.logo img {
  height: 40px;
  width: auto;
}

/* Styles pour les liens de navigation */
.nav-links {
  display: flex;
  list-style: none;
  gap: 20px;
}

.nav-links li a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  font-size: 1rem;
  position: relative;
  padding: 5px 0;
  transition: all 0.3s ease;
}

.nav-links li a::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 50%;
  width: 0;
  height: 2px;
  background: rgba(124, 77, 255, 0.5);
  transition: width 0.3s ease, left 0.3s ease;
}

.nav-links li a:hover::after {
  width: 100%;
  left: 0;
}

.nav-links li a:hover {
  color: rgba(255, 255, 255, 1);
}

.nav-links li a.active {
  color: #7C4DFF;
}

.nav-links li a.active::after {
  width: 100%;
  left: 0;
  background: #7C4DFF;
}

/* Styles pour le menu burger amélioré */
.burger-menu {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 25px;
  height: 20px;
  background: none;
  border: none;
  cursor: pointer;
}

.burger-menu span {
  display: block;
  height: 3px;
  width: 100%;
  background: linear-gradient(90deg, #fff, #7C4DFF);
  border-radius: 3px;
  transition: all 0.4s ease;
}

/* Animation du menu burger */
.burger-menu.active span:nth-child(1) {
  transform: rotate(45deg) translate(6px, 6px);
  background: #7C4DFF;
}

.burger-menu.active span:nth-child(2) {
  opacity: 0;
  transform: translateX(-20px);
}

.burger-menu.active span:nth-child(3) {
  transform: rotate(-45deg) translate(6px, -6px);
  background: #7C4DFF;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .burger-menu {
    display: flex;
    z-index: 1001;
  }

  .nav-links {
    display: none;
    flex-direction: column;
    position: fixed;
    top: 0;
    right: -100%;
    height: 100vh;
    width: 300px;
    background: rgba(18, 7, 44, 0.95);
    backdrop-filter: blur(15px);
    padding: 80px 40px;
    transition: right 0.4s ease;
  }

  .nav-links.active {
    right: 0;
    display: flex;
    box-shadow: -5px 0 30px rgba(0, 0, 0, 0.3);
  }

  .nav-links li {
    margin: 20px 0;
  }

  .nav-links li a {
    font-size: 1.2rem;
  }
}