.App {
  text-align: center;
}

/* Espacement entre les sections principales */
.content > section {
  margin: 0;
  padding: 160px 0;  /* Augmenté pour plus d'espace entre les sections */
  position: relative; /* Pour assurer une meilleure séparation */
}

.content > section:first-child {
  padding-top: 180px;
}

.content > section:last-child {
  padding-bottom: 120px;
}

/* Ajustements responsive */
@media (max-width: 1024px) {
  .content > section {
    padding: 140px 0;
  }
  
  .content > section:first-child {
    padding-top: 160px;
  }
}

@media (max-width: 768px) {
  .content > section {
    padding: 120px 0;
  }
  
  .content > section:first-child {
    padding-top: 140px;
  }
  
  .content > section:last-child {
    padding-bottom: 100px;
  }
}

@media (max-width: 480px) {
  .content > section {
    padding: 100px 0;
  }
  
  .content > section:first-child {
    padding-top: 120px;
  }
  
  .content > section:last-child {
    padding-bottom: 80px;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
