footer {
    background: rgba(18, 7, 44, 0.35);
    color: #fff;
    padding: 60px 0 40px;
    text-align: center;
    position: relative;
    backdrop-filter: blur(10px);
    border-top: 1px solid rgba(124, 77, 255, 0.1);
}

.footer-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 40px;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
    position: relative;
}

.footer-content::before {
    content: '';
    position: absolute;
    top: -20px;
    left: 50%;
    transform: translateX(-50%);
    width: 40px;
    height: 4px;
    background: linear-gradient(90deg, transparent, rgba(124, 77, 255, 0.5), transparent);
}

.footer-links {
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
    justify-content: center;
}

.footer-links a {
    color: rgba(255, 255, 255, 0.8);
    text-decoration: none;
    font-size: 1rem;
    transition: all 0.3s ease;
    position: relative;
    padding: 5px 0;
}

.footer-links a::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 2px;
    background: rgba(124, 77, 255, 0.5);
    transition: all 0.3s ease;
}

.footer-links a:hover {
    color: #fff;
    transform: translateY(-2px);
}

.footer-links a:hover::after {
    width: 100%;
    left: 0;
}

.footer-socials {
    display: flex;
    gap: 25px;
    margin: 10px 0;
}

.footer-socials a {
    color: rgba(124, 77, 255, 0.8);
    font-size: 1.8rem;
    transition: all 0.3s ease;
    padding: 10px;
    border-radius: 50%;
    background: rgba(124, 77, 255, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-socials a:hover {
    color: #fff;
    background: rgba(124, 77, 255, 0.2);
    transform: translateY(-3px);
    box-shadow: 0 5px 15px rgba(124, 77, 255, 0.2);
}

footer p {
    margin: 0;
    font-size: 0.9rem;
    color: rgba(255, 255, 255, 0.6);
    position: relative;
    padding: 15px 0;
}

footer p::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    height: 1px;
    background: linear-gradient(90deg, transparent, rgba(124, 77, 255, 0.3), transparent);
}

footer p .heart-icon {
    animation: heartbeat 1.5s ease-in-out infinite;
    display: inline-block;
}

@keyframes heartbeat {
    0% { transform: scale(1); }
    14% { transform: scale(1.1); }
    28% { transform: scale(1); }
    42% { transform: scale(1.1); }
    70% { transform: scale(1); }
}

@media (max-width: 900px) {
    .footer-content {
        gap: 35px;
        padding: 0 15px;
    }

    .footer-links {
        gap: 25px;
    }

    .footer-socials {
        gap: 22px;
    }
}

@media (max-width: 768px) {
    footer {
        padding: 50px 0 35px;
    }

    .footer-content {
        gap: 30px;
    }

    .footer-links {
        gap: 18px;
        flex-direction: row;
        justify-content: center;
        flex-wrap: wrap;
        max-width: 500px;
        margin: 0 auto;
    }

    .footer-links a {
        font-size: 0.9rem;
        padding: 3px 8px;
    }

    .footer-socials {
        gap: 18px;
    }

    .footer-socials a {
        font-size: 1.5rem;
        padding: 8px;
    }

    footer p {
        font-size: 0.85rem;
        padding: 12px 0;
        max-width: 300px;
        margin: 0 auto;
    }
}

@media (max-width: 600px) {
    .footer-content {
        gap: 25px;
        padding: 0 12px;
    }

    .footer-links {
        gap: 15px 20px;
        padding: 0 10px;
    }

    .footer-socials {
        gap: 15px;
    }

    .footer-socials a {
        font-size: 1.4rem;
        padding: 7px;
    }
}

@media (max-width: 480px) {
    footer {
        padding: 40px 0 30px;
    }

    .footer-content {
        gap: 20px;
    }

    .footer-links {
        display: none;
    }

    .footer-socials {
        gap: 12px;
        margin-top: 0;
    }

    .footer-socials a {
        font-size: 1.3rem;
        padding: 6px;
    }

    footer p {
        font-size: 0.8rem;
        padding: 10px 0;
        max-width: 280px;
        margin-top: 0;
    }
}

@media (max-width: 360px) {
    .footer-links {
        gap: 10px 15px;
    }

    .footer-links a {
        font-size: 0.8rem;
    }

    .footer-socials a {
        font-size: 1.2rem;
        padding: 5px;
    }

    footer p {
        font-size: 0.75rem;
        max-width: 260px;
    }
}
