.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
    backdrop-filter: blur(5px);
}

.popup-overlay.active {
    opacity: 1;
    visibility: visible;
}

.popup-content {
    background: rgba(31, 26, 45, 0.95);
    border: 2px solid rgba(124, 77, 255, 0.3);
    border-radius: 20px;
    padding: 30px;
    text-align: center;
    max-width: 400px;
    position: relative;
    transform: translateY(20px) scale(0.95);
    opacity: 0;
    transition: all 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.popup-overlay.active .popup-content {
    transform: translateY(0) scale(1);
    opacity: 1;
}

.popup-icon {
    width: 70px;
    height: 70px;
    background: rgba(124, 77, 255, 0.1);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto 20px;
    position: relative;
}

.popup-icon::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: 2px solid rgba(124, 77, 255, 0.3);
    animation: ripple 1.5s linear infinite;
}

.popup-check {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    border: 2px solid rgba(124, 77, 255, 1);
}

.popup-check::after {
    content: '';
    width: 16px;
    height: 8px;
    position: absolute;
    border: 2px solid rgba(124, 77, 255, 1);
    border-top: none;
    border-right: none;
    background: transparent;
    top: 8px;
    left: 6px;
    transform: rotate(-45deg);
    animation: checkmark 0.8s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
    opacity: 0;
}

@keyframes checkmark {
    0% {
        opacity: 0;
        transform: rotate(-45deg) scale(0);
    }
    50% {
        opacity: 1;
        transform: rotate(-45deg) scale(1.2);
    }
    100% {
        opacity: 1;
        transform: rotate(-45deg) scale(1);
    }
}

.popup-title {
    color: #fff;
    font-size: 1.5rem;
    margin-bottom: 10px;
    font-weight: 600;
}

.popup-message {
    color: rgba(255, 255, 255, 0.8);
    margin-bottom: 25px;
    line-height: 1.5;
}

.popup-button {
    background: rgba(124, 77, 255, 0.1);
    border: 1.5px solid rgba(124, 77, 255, 0.4);
    color: #fff;
    padding: 10px 25px;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 0.95rem;
}

.popup-button:hover {
    background: rgba(124, 77, 255, 0.2);
    border-color: rgba(124, 77, 255, 0.6);
    transform: translateY(-2px);
}

@keyframes ripple {
    0% {
        transform: scale(1);
        opacity: 0.5;
    }
    100% {
        transform: scale(1.5);
        opacity: 0;
    }
}

@keyframes bounce {
    0% {
        transform: scale(0) rotate(-45deg);
    }
    70% {
        transform: scale(1.2) rotate(10deg);
    }
    100% {
        transform: scale(1) rotate(0);
    }
}

@media (max-width: 480px) {
    .popup-content {
        margin: 0 20px;
        padding: 25px;
    }
    
    .popup-icon {
        width: 60px;
        height: 60px;
    }
    
    .popup-title {
        font-size: 1.3rem;
    }
}